  width: calc(100% - 30px);
  height: auto;
  background-color: #fcfcfc;
  border-radius: 10px;
  outline: 2px solid #f8d24e;
  flex-direction: column;
  align-items: center;
  padding: 28px;
  transition: transform .25s linear;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.2);
}

